<template>
		<a-form :form="form" class="login-form" @submit="handleSubmit">
			<a-form-item>
				<a-input v-decorator="['userName',{ rules: [{ required: true, message: 'Please input your username!' }] },]" placeholder="Username">
					<a-icon slot="prefix" type="user" style="color: rgba(0,0,0,.25)" />
				</a-input>
			</a-form-item>
			<a-form-item>
				<a-input v-decorator="['password',{ rules: [{ required: true, message: 'Please input your Password!' }] },]" type="password" placeholder="Password">
					<a-icon slot="prefix" type="lock" style="color: rgba(0,0,0,.25)" />
				</a-input>
			</a-form-item>
			<a-form-item>
				
				<a-button type="primary" html-type="submit" class="login-form-button">
					登录
				</a-button>
				Or
				<router-link to="/result">
					<a href="">
						注册
					</a>
				</router-link>
				
			</a-form-item>
		</a-form>
</template>

<script>
	import * as api from "@/api/index.js"
	export default {
		data(){
			return{
				from:""
			}
		},
		beforeCreate() {
			this.form = this.$form.createForm(this, {
				name: 'normal_login'
			});
		},
		methods: {
			async handleSubmit(e) {
				let that = this
				e.preventDefault();
				that.form.validateFields((err,values)=>{
					if(!err){
						that.apilogin(values)
						// window.console.log(values)
					}
				})
			},
			async apilogin(values){
				let that = this
				let res = await api.login({
					"name":values.userName,
					"pwd":values.password
				})
				window.console.log(res)
				if(res.data.code === 1000){
					that.$message.loading("登录中",2.5)
					.then(()=>{
						that.$message.success("登录成功")
						that.$store.commit("userModel/addtoken",res.data.token)
						that.$router.push("/")
					})
				}else{
					that.$message.error("请检查账号或密码")
				}
			}
		},
	}
</script>

<style lang="scss">
	.login-form {
		width: 400px;
		margin: 0 auto;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}
</style>
